import React, { useState, useEffect } from "react";
import { Switch, Route, HashRouter as Router } from "react-router-dom";
import Axios from 'axios'
import {
  Home,
  Login,
  Register,
  Invoice,
  Admin,
  Edit,
  Create,
  Confirm,
  TransList,
  Customer,
  Invite,
} from "../pages";
//import { PageTransition } from "@steveeeie/react-page-transition";
import UserContext from "../context/UserContext";
import { ToastProvider } from "react-toast-notifications";

export default function Index() {
  const [loginStatus, setLoginStatus] = useState({
    token: undefined,
    email: undefined,
    customerId:''
  });
  useEffect(() => {
    const checkLoggedIn = async () => {
      let token = localStorage.getItem("auth-token");
      if (token === null) {
        localStorage.setItem("auth-token", "");
        token = "";
      }
      const tokenRes = await Axios.post(
        "/users/tokenIsValid",
        null,
        { headers: { "x-auth-token": token } }
      );
      if (tokenRes.data) {
        const userRes = await Axios.get("/users/", {
          headers: { "x-auth-token": token },
        });
        setLoginStatus({
          token,
          email: userRes.data.email,
          customerId:''
        });
      }
    };

    checkLoggedIn();
  }, []);
 
  //const [loginStatus, setLoginStatus] = useState(false);
  const [pdf, setPdf] = useState('');
  const [conf, setConf] = useState(true);
  return (
    <Router>
      <UserContext.Provider value={{ loginStatus, setLoginStatus, pdf, setPdf, conf, setConf }}>
        <ToastProvider>
          {/* <Route
            render={({ location }) => {
              return (
                <PageTransition
                  preset="moveToLeftFromRight"
                  transitionKey={location.pathname}
                > */}
                  <Switch>
                    <Route exact path="/" component={Home}></Route>
                    <Route exact path="/login" component={Login}></Route>
                    <Route exact path="/register" component={Register}></Route>
                    <Route exact path="/invoice" component={Invoice}></Route>
                    <Route exact path="/admin" component={Admin}></Route>
                    <Route exact path="/admin/edit/:id" component={Edit}></Route>
                    <Route exact path="/admin/new" component={Create}></Route>
                    <Route exact path="/confirm/:id" component={Confirm}></Route>
                    <Route exact path="/translist" component={TransList}></Route>
                    <Route exact path="/customer" component={Customer}></Route>
                    <Route exact path="/invite" component={Invite}></Route>
                  </Switch>
                {/* </PageTransition>
              );
            }}
          /> */}
        </ToastProvider>
      </UserContext.Provider>
    </Router>
  );
}
